<template>
    <div>
      <h2 style="width:80%;text-align: center">小区充值系数</h2>
      <el-table border :data="tableData" style="width: 80%;margin-top: 15px;">
        <el-table-column type="index" label="序号" width="60px" align="center" ></el-table-column>
        <el-table-column prop="plotName" label="小区名称" min-width="20%" align="center"></el-table-column>
        <el-table-column prop="plotFactor" label="单价（元/吨）" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="operation" label="操作" min-width="10%" align="center" >
          <template slot-scope="scope">
            <el-button type="primary" style="padding:10px 15px 10px 15px;"
                       @click="updatePlotFactor(scope.$index, scope.row)">修改
            </el-button>
          </template>
        </el-table-column>
        <el-table-column label="备注" min-width="15%" align="center">
        </el-table-column>
      </el-table>
      <el-dialog title="修改单价" :visible.sync="dialogFormVisible">
        <el-form :label-position="labelPosition" label-width="80px">
          <el-form-item label="小区名称">
            <el-input v-model="updateForm.plotName" autocomplete="on" disabled></el-input>
          </el-form-item>
          <el-form-item label="原单价">
            <el-input v-model="updateForm.oldFactor" autocomplete="on" disabled></el-input>
          </el-form-item>
          <el-form-item label="现单价">
            <el-input v-model="updateForm.newFactor" type="number" autocomplete="on"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="ensureUpdate">确 定</el-button>
        </div>
      </el-dialog>
    </div>
</template>

<script>
export default {
  name: 'plotFactor',
  data () {
    return {
      tableData: [{
        plotName: '荆州新天地',
        plotNum: '00',
        plotFactor: 0.7
      },
      {
        plotName: '荆门新天城',
        plotNum: '01',
        plotFactor: 0.5
      },
      {
        plotName: '荆门碧桂园',
        plotNum: '02',
        plotFactor: 0.8
      }],
      labelPosition: 'right',
      dialogFormVisible: false,
      updateForm: {
        plotName: null,
        plotNum: null,
        oldFactor: null,
        newFactor: null
      }
    }
  },
  methods: {
    updatePlotFactor (index, row) {
      // console.log(index, row)
      this.updateForm.plotName = row.plotName
      this.updateForm.plotNum = row.plotNum
      this.updateForm.oldFactor = row.plotFactor
      this.updateForm.newFactor = null
      this.dialogFormVisible = true
    },
    ensureUpdate () {
      if (this.updateForm.newFactor === null || this.updateForm.newFactor === '') {
        alert('请输入新的充值系数')
      } else if (!(/^[1-9]+[0-9]*\.[0-9]+$/.test(this.updateForm.newFactor)) &&
        !(/^0\.[0-9]+$/.test(this.updateForm.newFactor)) &&
        !(/^[1-9]+[0-9]*$/.test(this.updateForm.newFactor))) {
        alert('请输入合法有效的充值系数')
      } else {
        // console.log(this.updateForm)
        this.dialogFormVisible = false
      }
    }
  }
}
</script>

<style scoped>
</style>



